import React from 'react';
import Gallery from './gallery';
import GallerySource from '../portfolio.json';

class Portfolio extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            galleryClicked: null,
            currentSlide: 1
        }
    }


    handleClick = (e) => {
        console.log(e.target.id);
        switch(e.target.id) {
            case "1": 
                console.log("Loading Modal for Gallery Item 1");
                this.setState({ galleryClicked: "0"});
                break;
            case "2": 
                console.log("Loading Modal for Gallery Item 2");
                this.setState({ galleryClicked: "1"});
                break;
            case "3": 
                console.log("Loading Modal for Gallery Item 3");
                this.setState({ galleryClicked: "2"});
                break;
            case "4": 
                console.log("Loading Modal for Gallery Item 4");
                this.setState({ galleryClicked: "3"});
                break;
            case "5": 
                console.log("Loading Modal for Gallery Item 5");
                this.setState({ galleryClicked: "4"});
                break;
            case "6": 
                console.log("Loading Modal for Gallery Item 5");
                this.setState({ galleryClicked: "5"});
                break;
            case "7": 
                console.log("Loading Modal for Gallery Item 5");
                this.setState({ galleryClicked: "6"});
                break;
            case "8": 
                console.log("Loading Modal for Gallery Item 5");
                this.setState({ galleryClicked: "7"});
                break;
            case "9": 
                console.log("Loading Modal for Gallery Item 5");
                this.setState({ galleryClicked: "8"});
                break;
            case "10": 
                console.log("Loading Modal for Gallery Item 5");
                this.setState({ galleryClicked: "9"});
                break;
            default:
                return null;
        }
    }
                
    currentModal = () => {
        const gallery = GallerySource.gallery;
        let cardSelected = this.state.currentSlide;
        switch(this.state.galleryClicked) {
            case "0": 
                cardSelected = "0";
                break;
            case "1": 
                cardSelected = "1";
                break;
            case "2": 
                cardSelected = "2";
                break;
            case "3": 
                cardSelected = "3";
                break;
            case "4": 
                cardSelected = "4";
                break;
            case "5": 
                cardSelected = "5";
                break;
            case "6": 
                cardSelected = "6";
                break;
            case "7": 
                cardSelected = "7";
                break;
            case "8": 
                cardSelected = "8";
                break;
            case "9": 
                cardSelected = "9";
                break;
            case "10": 
                cardSelected = "10";
                break;
            default:
                return null;
        }
        return (
            <>
                <div className="modal-gallery">
                    <span className="close cursor" onClick={this.closeModal}>&times;</span>
                    <div className="current-slide">{this.slideSelection()}</div>
                    <div className="modal-content">
                        {gallery[cardSelected].subSection.image1 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 1})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image1.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image2 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 2})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image2.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image3 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 3})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image3.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image4 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 4})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image4.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image5 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 5})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image5.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image6 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 6})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image6.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image7 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 7})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image7.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image8 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 8})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image8.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image9 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 9})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image9.src)} alt={gallery[cardSelected].alt} /></div> }
                        {gallery[cardSelected].subSection.image10 && <div className="slideshow" onClick={() => {this.setState({currentSlide: 10})}}><img src={require("../images/portfolio" + gallery[cardSelected].subSection.image10.src)} alt={gallery[cardSelected].alt} /></div> }
                    </div>
                    {/* <div className="prev" onClick="plusSlides(-1)">&#10094;</div>
                    <div className="next" onClick="plusSlides(1)">&#10095;</div> */}
                </div> 
                <div className="modal-background" onClick={this.closeModal}></div>
                </>
        )
    }

    slideSelection = () => {
        const gallery = GallerySource.gallery;
        let cardSelected;
        let gallerySelected = this.state.galleryClicked;
        switch(this.state.currentSlide) {
            case 1: 
                cardSelected = 0;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image1.src)} alt={gallery[gallerySelected].alt} />;
            case 2: 
                cardSelected = 1;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image2.src)} alt={gallery[gallerySelected].alt} />;
            case 3: 
                cardSelected = 2;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image3.src)} alt={gallery[gallerySelected].alt} />;
            case 4: 
                cardSelected = 3;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image4.src)} alt={gallery[gallerySelected].alt} />;
            case 5: 
                cardSelected = 4;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image5.src)} alt={gallery[gallerySelected].alt} />;
            case 6: 
                cardSelected = 5;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image6.src)} alt={gallery[gallerySelected].alt} />;
            case 7: 
                cardSelected = 6;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image7.src)} alt={gallery[gallerySelected].alt} />;
            case 8: 
                cardSelected = 7;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image8.src)} alt={gallery[gallerySelected].alt} />;
            case 9: 
                cardSelected = 8;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image9.src)} alt={gallery[gallerySelected].alt} />;
            case 10: 
                cardSelected = 9;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image10.src)} alt={gallery[gallerySelected].alt} />;
            default:
                cardSelected = 10;
                return <img src={require("../images/portfolio" + gallery[gallerySelected].subSection.image11.src)} alt={gallery[gallerySelected].alt} />;
        }
    }

    // Close the Modal
    closeModal = () => {
        console.log("Close");
        this.setState({ galleryClicked: 0 });
    }


    render() {
        return (
            <div id="portfolio"style={{paddingTop: "100px"}}>
                <h1 className="section-title">Work</h1>
                <Gallery galleryClick={this.handleClick}/>
                {this.currentModal()}
                
            </div>
        );
    }
}

export default Portfolio;